<template>
  <div style="max-width: 100%;box-sizing: border-box" class="getMedicine">
    <myHeader
      :isBack="true"
      backPath="/business"
      title="我要领药"
      subtitle="记录"
      subtitlePath="/getMedicineRecord">
    </myHeader>

    <div class="seat-head"></div>
    <div class="list-block">
      <mt-cell title="批次号：" @click.native="selectBatchNum">
        {{batchNumber}}
        <span class="img"></span>
      </mt-cell>
      <mt-cell title="申请日期：" @click.native="selectApplyDate">
        <span class="color-text">
          {{applyDateText}}
        </span>
        <span class="img"></span>
      </mt-cell>

      <mt-cell title="领药原因：" @click.native="selectNeedMedicineReason">
        {{needMedicineReason}}
        <span class="img"></span>
      </mt-cell>

      <mt-cell title="语音说话：">
        <span style="display: inline-block;padding: 0 0.25rem;margin-right: 32px;" @click="playAudio" v-show="isShowBroadcastBtn">
          <img style="width:.23rem;" src="@/assets/righticon.png">
        </span>
        <img src="@/assets/icon-talk.png" @click="showTalking">
      </mt-cell>
    </div>

    <div class="select-type">
      <div class="title">
        是否委托他人领取
      </div>

      <div class="radio-select-box">
        <div class="radio-item">
          <span>否</span>
          <img v-if="isNeedOther" @click="switchNeedOtherHandler('isNeedOther')" src="@/assets/select-icon.png">
          <img v-else @click="switchNeedOtherHandler('isNeedOther')" src="@/assets/noSelect-icon.png">
        </div>
        <div class="radio-item">
          <span>是</span>
          <img v-if="isNotNeedOther" @click="switchNeedOtherHandler('isNotNeedOther')" src="@/assets/select-icon.png">
          <img v-else @click="switchNeedOtherHandler('isNotNeedOther')" src="@/assets/noSelect-icon.png">
        </div>
      </div>
      <div v-if="isNotNeedOther && isShowEntrust">
        <mt-cell title="委托领取人姓名：" @click.native="selectEntrust">
          {{clientName}}
          <span class="img"></span>
        </mt-cell>
        <mt-cell title="委托领取人身份证号：" :value="clientID"></mt-cell>
      </div>
    </div>

    <SubmitBtn @click.native.prevent="submitPageData()"
               :btnText="submitBtnText"
               :customStyle="myCustomStyle"></SubmitBtn>

    <div class="my-prop">
      <!--选择批次-->
      <mt-popup
        v-model="switchBatchNumPopup"
        position="bottom">
        <mt-picker :slots="batchNumSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="batchPicker">
          <span @click="handleBatchConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>
      

      <!--选择需要药物的原因-->
      <mt-popup
        v-model="switchNeedMedicinePopup"
        position="bottom">
        <mt-picker :slots="needMedicineSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="needMedicinePicker">
          <span @click="handleNeedMedicineConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>

      <!--选择委托人-->
      <mt-popup
        v-model="switchEntrustPopup"
        position="bottom">
        <mt-picker :slots="needEntrustSlots"
                   value-key="name"
                   :show-toolbar="true"
                   ref="entrustPicker">
          <span @click="handleEntrustConfirm" class="sure-btn">确认</span>
        </mt-picker>
      </mt-popup>

    </div>
    <mt-popup ref="talking"  v-model="switchTalking"  position="bottom" class="talking-container">
     <div class="talking-pop">
        <div class="talkingHeader">
          <div class="left" @click="cancelTalking">取消</div>
          录音
          <div class="right" @click="finishTalking">完成</div>
        </div>
        <div class=" talking-body">
          <div class="control">
            <span class="stop" @click="stopTalking" v-if="!isShowBroadcastBtn">停止</span>
            <span class="stop" @click="pauseTalking" v-if="isShowBroadcastBtn">暂停</span>
            <span class="start" @click="startTalking" v-if="!isShowBroadcastBtn && switchIcon">开始录音</span>
            <span class="stoping" v-if="!isShowBroadcastBtn && !switchIcon">录音中</span>
            <span class="start" @click="broadcastTalking" v-if="isShowBroadcastBtn && switchIcon1">播放录音</span>
            <span class="stoping" v-if="isShowBroadcastBtn && !switchIcon1">播放中</span>
            <span class="reset" @click="resetTalking">重置</span>
          </div>
        </div>
      </div>
    </mt-popup>

    <!--选择申请日期-->
    <mt-datetime-picker
      ref="applyDatePicker"
      type="date"
      v-model="applyDate"
      @confirm="applyDateConfirm"></mt-datetime-picker>
    
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'
  import myHeader from '@/components/Header'
  import SubmitBtn from '@/components/SubmitBtn'
  import {formatDate} from '@/utils/tool'
  import {inputLimitNum, submitValidator} from '@/utils/validator'
  import {getBatchNumberList, saveDrug, queryEntrustList} from '@/api/materialApply'
  import {mapGetters} from 'vuex'
  import { Indicator } from 'mint-ui'
  import subMitBtnMixins from '@/mixins/subMitBtnMixins'

  export default {
    name: 'getMedicine',
    mixins: [subMitBtnMixins],
    data () {
      return {
        batchNumber: '',
        batchId: '',
        switchBatchNumPopup: false,
        batchNumSlots: [{
          values: [{
            id: '',
            name: ''
          }],
          textAlign: 'center'
        }],
        applyDateText: formatDate(new Date(), 'yyyy-MM-dd'),
        applyDate: new Date(),
        needMedicineReason: '',
        needMedicineReasonId: '',
        switchTalking: false,
        switchNeedMedicinePopup: false,
        needMedicineSlots: [{
          values: [
            {id: 1, name: '免疫'},
            {id: 2, name: '保健'},
            {id: 3, name: '抗应激'},
            {id: 4, name: '治疗'},
            {id: 5, name: '消毒'},
            {id: 6, name: '器械'},
          ],
          textAlign: 'center',
          defaultIndex:1
        }],
        isNeedOther: true,
        isNotNeedOther: false,
        isShowEntrust: false,
        isEntrust: false,
        clientName: '',
        clientID: '',
        entrustorId: '',
        switchEntrustPopup: false,
        needEntrustSlots: [{
          values: [{id: '', name: '', idcard: ''}],
          textAlign: 'center',
          defaultIndex:0
        }],
        localId: '',
        serverId: '',
        isShowBroadcastBtn: false,
        switchIcon: true,
        switchIcon1: true
      }
    },
    components: {
      SubmitBtn,
      myHeader
    },
    created () {
      this._getBatchNumberList();
      this._initData();
    },
    mounted(){
      this.initWxConfig();
    },
    computed:{
      ...mapGetters([
        'signature'
      ])
    },
    methods: {
      _initData() {
        this.needMedicineReason = this.needMedicineSlots[0].values[1].name;
        this.needMedicineReasonId = this.needMedicineSlots[0].values[1].id;
      },
      selectBatchNum () {
        if (this.batchNumSlots[0].values[0].name) {
          this.switchBatchNumPopup = !this.switchBatchNumPopup;
        } else {
          this.$toast({
            message: '无可选批次，请核实业务场景！',
            duration: 1000
          });
        }
      },
      showTalking () {
        this.switchTalking = !this.switchTalking
      },
      _getBatchNumberList () {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        getBatchNumberList().then((res) => {
          Indicator.close();
          if (res.data && res.data.length) {
            this.batchNumSlots[0].values = res.data.map((item, index) => {
              return {
                name: item.name,
                id: item.id
              }
            });

            this.batchNumber = this.batchNumSlots[0].values[0].name;
            this.batchId = this.batchNumSlots[0].values[0].id;
          }
        }).catch(err => {
          Indicator.close();
        });

      },
      _queryEntrustList () {
        queryEntrustList().then(res => {
          Indicator.close();
          let data = res.data;
          if (data && data.list.length) {
            this.isShowEntrust = true;
            this.needEntrustSlots[0].values = data.list.map(item => {
              return {
                name: item.name,
                id: item.id,
                idcard: item.idcard
              }
            });

            this.clientName = this.needEntrustSlots[0].values[0].name;
            this.clientID = this.needEntrustSlots[0].values[0].idcard;
            this.entrustorId = this.needEntrustSlots[0].values[0].id;
          } else {
            this.$toast({
              message: '无委托人信息，请维护养户档案！',
              duration: 1000
            });
            this.isNeedOther = true;
            this.isNotNeedOther = false;
            this.isEntrust = false;
            this.isShowEntrust = false;
            Indicator.close();
          }
        }).catch(err => {
          this.isNeedOther = true;
          this.isNotNeedOther = false;
          this.isEntrust = false;
          this.isShowEntrust = false;
          this.$toast({
            message: '无委托人信息，请维护养户档案！',
            duration: 1000
          });
          Indicator.close();
        });
      },
      handleBatchConfirm() {
        this.batchNumber = this.$refs.batchPicker.getValues()[0].name;
        this.batchId = this.$refs.batchPicker.getValues()[0].id;
        this.switchBatchNumPopup = false;
      },
      selectApplyDate () {
        this.$refs.applyDatePicker.open()
      },
      applyDateConfirm (date) {
        this.applyDateText = formatDate(date, 'yyyy-MM-dd')
      },
      selectNeedMedicineReason () {
        this.switchNeedMedicinePopup = !this.switchNeedMedicinePopup
      },
      handleNeedMedicineConfirm() {
        this.needMedicineReason = this.$refs.needMedicinePicker.getValues()[0].name;
        this.needMedicineReasonId = this.$refs.needMedicinePicker.getValues()[0].id;
        this.switchNeedMedicinePopup = false;
      },
      selectEntrust () {
        if (this.needEntrustSlots[0].values[0].name) {
          this.switchEntrustPopup = !this.switchEntrustPopup;
        } else {
          this.$toast({
            message: '无委托人信息，请维护养户档案！',
            duration: 1000
          });
        }
      },
      handleEntrustConfirm() {
        this.clientName = this.$refs.entrustPicker.getValues()[0].name;
        this.clientID = this.$refs.entrustPicker.getValues()[0].idcard;
        this.entrustorId = this.$refs.entrustPicker.getValues()[0].id;
        this.switchEntrustPopup = false;
      },
      switchNeedOtherHandler (type) {
        if (type === 'isNeedOther') {
          if (!this.isNeedOther) {
            this.isNeedOther = true;
            this.isNotNeedOther = false;
            this.isEntrust = false;
          }
        } else if (type === 'isNotNeedOther') {
          if (!this.isNotNeedOther) {
            this.isNotNeedOther = true;
            this.isNeedOther = false;
            this.isEntrust = true;
            this._queryEntrustList()
          }
        }
      },
      _saveDrug () {
        const data = {
          batchId: this.batchId,
          batchNo: this.batchNumber,
          applydate: this.applyDateText,
          recipientReason: this.needMedicineReasonId,
          serverId: this.serverId,
          isEntrust: this.isEntrust,
          entrustIdcard: this.clientID,
          entrustName: this.clientName,
          entrustNameId: this.entrustorId
        };

        saveDrug(data).then((res) => {
          this.$toast({
            message: '提交成功！',
            duration: 1000
          });
  
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
          this.$router.push('/getMedicineRecord');
          
        }).catch(err => {
          this.isDisabledSubmit = false;
          this.submitBtnText = '提交';
        });
      },
      submitPageData () {
        let flag = submitValidator([{
          val: this.batchNumber,
          rule: ['required'],
          getTipText: '批次号'
        },{
          val: this.serverId,
          rule: ['required'],
          getTipText: '语音说话'
        }]);

        if (!flag) return;
        
        if (!this.isDisabledSubmit) {
          this.isDisabledSubmit = true;
          this.submitBtnText = '提交中...';
          this._saveDrug();
        }
        
      },
      initWxConfig() {
        wx.config({
          debug: false, //调试微信接口，上线可以关闭
          appId: this.signature.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
          timestamp: this.signature.timestamp, // 必填，生成签名的时间戳
          nonceStr: this.signature.nonceStr, // 必填，生成签名的随机串
          signature: this.signature.signature,// 必填，签名，见附录1
          jsApiList: [
            'startRecord',
            'stopRecord',
            'onVoiceRecordEnd',
            'playVoice',
            'pauseVoice',
            'stopVoice',
            'onVoicePlayEnd',
            'uploadVoice'
          ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
      },
      cancelTalking(){
        this.switchTalking = false;
      },
      finishTalking(){
        this.switchTalking = false;
        this.switchIcon = true;
        this.switchIcon1 = true;
        this.pauseTalking();
      },
      stopTalking(){ //停止录音
        let _this = this;
        this.isShowBroadcastBtn = true;
        this.switchIcon = true;
        wx.stopRecord({
          success: function (res) {
            _this.localId = res.localId;
            wx.uploadVoice({
              localId: _this.localId,
              success: function (res) {
                _this.serverId = res.serverId; // 返回音频的服务器端ID
              }
            });
          },
          fail: function (error) {

          }
        });
      },
      startTalking(){ //开始录音
        let _this = this;
        this.switchIcon = false;
        wx.startRecord({
          success: function(){
          
          },
          cancel: function () {
            _this.$toast({
              message: '用户拒绝授权录音！',
              duration: 500
            });
          }
        });
      },
      playAudio () { //播放录音
        let _this = this;
        wx.playVoice({
          serverId: _this.serverId,
          localId: _this.localId
        });
      },
      resetTalking(){ //重置录音
        this.isShowBroadcastBtn = false;
        this.switchIcon = true;
        this.switchIcon1 = true;
        this.localId = '';
        this.serverId = '';
        this.pauseTalking();
      },
      broadcastTalking() {//播放录音
        let _this = this;
        this.switchIcon1 = false;
        wx.playVoice({
          serverId: _this.serverId,
          localId: _this.localId
        });
      },
      pauseTalking() { //暂停播放录音
        let _this = this;
        this.switchIcon1 = true;
        wx.pauseVoice({
          localId: _this.localId,
          serverId: _this.serverId
        });
      }
    }
  }
</script>

<style lang="scss">
  .getMedicine {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }

    .list-block {
      font-size: .16rem;
      margin-top: .1rem;
      background-color: #fff;
      .img {
        width: .1rem;
        height: .06rem;
        margin-left: .08rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
      }
    }

    .select-type {
      background-color: #fff;
      margin-top: .1rem;
      font-size: .16rem;
      .title {
        height: .45rem;
        padding: .1rem .15rem;
        border-bottom: 1px solid #dddddd;
      }
      .radio-select-box {
        padding-left: .15rem;
        border-bottom: 1px solid #dddddd;
        .radio-item {
          display: flex;
          justify-content: space-between;
          height: .52rem;
          line-height: .52rem;
          padding-left: .20rem;
          padding-right: .15rem;
          &:first-child {
            border-bottom: 1px solid #dddddd;
          }
          img {
            align-self: center;
          }
        }
      }
      .img {
        width: .1rem;
        height: .06rem;
        margin-left: .08rem;
        background: url('~@/assets/icon-next.png') no-repeat;
        background-size: 100%;
      }
    }
    .apply-detail {
      font-size: .16rem;
      background-color: #fff;
      margin-top: .1rem;
      .top {
        height: .45rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 .15rem;
        border-bottom: 1px solid #EBEBEB;
      }
      .detail-item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: .54rem;
        padding: 0 .35rem;
        border-bottom: 1px solid #dddddd;
        font-size: .14rem;
        .item-name {
          width: 70%;
        }
        .item-num {
          width: 30%;
        }
      }
    }
    .my-prop {
      .mint-popup {
        width: 100%;
        height: 25%;
      }
  
      /deep/ .picker-toolbar {
        height: 0;
      }
  
      .sure-btn {
        position: absolute;
        top: .05rem;
        right: .05rem;
        padding: .05rem .20rem;
        font-size: .16rem;
        z-index: 99;
      }
    }
    .talking-container {
      width: 100%;
      text-align: center;
      font-size: .15rem;
      .talking-pop {
        width: 100%;
        .talkingHeader {
          display: flex;
          justify-content: space-between;
          color: #000000;
          line-height: .4rem;
          height: .4rem;
          padding: 0 .15rem;
          border-bottom: 1px solid #dddddd;
          .left, .right {
            color: #888888;
          }
        }
        .talking-body {
          .time{
            padding-top:.26rem;
            font-size: .2rem;
          }
          .range{
            width: 3rem;
            height: .04rem;
            background: #dddddd;
            margin:.1rem 10% .4rem;
          }
          .control{
            display: flex;
            justify-content: space-between;
            margin:.1rem 12% .4rem;
            margin-top: .5rem;
            .stop{
              display: inline-block;
              width: .3rem;
              text-align: center;
              &:before{
                content: "";
                margin-left: .09rem;
                width: .12rem;
                height: .21rem;
                display: table;
                margin-bottom: .09rem;
                border-left:3px solid #fe7120;
                border-right:3px solid #fe7120;
              }
            }
            .stoping {
              display: inline-block;
              width: .8rem;
              text-align: center;
              position: relative;
              &:before{
                content: "";
                margin-left: .35rem;
                width: .12rem;
                height: .21rem;
                display: table;
                margin-bottom: .09rem;
                border-left:3px solid #fe7120;
                border-right:3px solid #fe7120;
              }
            }
            .start{
              display: inline-block;
              width: .8rem;
              text-align: center;
              position: relative;
              top:.3rem;
              &:before{
                content: "";
                margin-right: .14rem;
                width: 0;
                height: 0;
                border-width: 12px 0 12px 16px;
                border-color: transparent transparent transparent #FE7120;
                border-style: solid;
                position: absolute;
                bottom:.58rem;
                left: .34rem;
              }
            }
            .reset{
              display: inline-block;
              width: .3rem;
              text-align: center;
              &:before{
                content: "";
                margin-left: .06rem;
                width: .2rem;
                height: .2rem;
                display: table;
                margin-bottom: .09rem;
                border:3px solid #fe7120;
              }
            }
          }

        }

      }
    }

  }
</style>



